
html {
  height: 100%;
  width: 100%;
}
body {
  position: relative;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #333;
  background-color: #fff;
  margin: 0;
  padding: 0;
  height: 100%;
}
.layout {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  position: absolute;
  height: 100%;
  width: 100%;
  background: linear-gradient(to top left, #6e203a 20%, #303f92 50%, #520c72 80%);
}
.ui-content {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  background-color: rgba(48, 48, 48, 0.719);
  height: 100%;
  width: 100%;
}
